.dialog {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	gap: 15px;
	height: 100%;
	padding: 20px 10px;
	overflow-y: auto;

	&::-webkit-scrollbar {
		width: 7px;
	}

	&::-webkit-scrollbar-thumb {
		background: $grey-9c;
		border-radius: 5px;
	}
}