.user {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	min-width: 50px;
	max-width: 50px;
	height: 50px;
	border-radius: 50%;
	box-shadow: 3px 3px 10px 0 rgba(0, 0, 0, 0.2);
	color: $white;
	font-weight: 500;
	text-transform: uppercase;

	&__new-messages-count {
		position: absolute;
		bottom: -3px;
		right: -3px;
	}
}