@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0);
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Montserrat", sans-serif;
  background-color: #ffffff;
  color: #000000;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  overflow: hidden;
  position: relative;
  z-index: 1;
}

a {
  color: #000000;
  text-decoration: none;
}

ul,
ol,
nav {
  list-style: none;
  padding: 0;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

[hidden] {
  display: none !important;
}

/* Blocks */
.new-messages-count {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 18px;
  max-width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: #e31010;
  font-size: 9px;
}

.user {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 50px;
  max-width: 50px;
  height: 50px;
  border-radius: 50%;
  box-shadow: 3px 3px 10px 0 rgba(0, 0, 0, 0.2);
  color: #ffffff;
  font-weight: 500;
  text-transform: uppercase;
}
.user__new-messages-count {
  position: absolute;
  bottom: -3px;
  right: -3px;
}

.user-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  min-height: 102px;
  padding: 8px 25px;
  border-top-right-radius: 20px;
  background-color: #fafbff;
  border-bottom: 2px solid rgba(112, 124, 151, 0.1);
}
@media only screen and (max-width: 576px) {
  .user-info {
    gap: 10px;
    padding: 10px;
  }
}
.user-info__left {
  display: flex;
  align-items: center;
  gap: 20px;
}
@media only screen and (max-width: 576px) {
  .user-info__left {
    gap: 10px;
  }
}
.user-info__name {
  font-size: 18px;
  line-height: 1.2;
  text-transform: capitalize;
}
@media only screen and (max-width: 576px) {
  .user-info__name {
    font-size: 16px;
  }
}
.user-info__status {
  color: #18c900;
  font-size: 14px;
  margin-top: 5px;
}
.user-info__status--offline {
  color: #e31010;
}

.dialog-row {
  position: relative;
  display: inline-block;
  max-width: 75%;
  background: linear-gradient(90deg, #60a9f6 0%, #2a8bf2 100%);
  box-shadow: 10px 10px 25px rgba(42, 139, 242, 0.1), 15px 15px 35px rgba(42, 139, 242, 0.05), 10px 10px 50px rgba(42, 139, 242, 0.1);
  border-radius: 0 10px 10px 10px;
  padding: 15px 15px 20px;
}
.dialog-row__text {
  color: #ffffff;
  font-size: 14px;
  line-height: 1.5;
}
.dialog-row__time {
  position: absolute;
  bottom: 4px;
  right: 10px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 10px;
}
.dialog-row--personal {
  align-self: flex-end;
  background: #ffffff;
  border: 1px solid rgba(112, 124, 151, 0.25);
  box-shadow: 10px 10px 25px rgba(112, 124, 151, 0.05), 15px 15px 35px rgba(112, 124, 151, 0.05);
}
.dialog-row--personal .dialog-row__text {
  color: #000000;
}
.dialog-row--personal .dialog-row__time {
  color: rgba(0, 0, 0, 0.7);
}

.dialog {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 15px;
  height: 100%;
  padding: 20px 10px;
  overflow-y: auto;
}
.dialog::-webkit-scrollbar {
  width: 7px;
}
.dialog::-webkit-scrollbar-thumb {
  background: #9c9c9c;
  border-radius: 5px;
}

.send {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  width: calc(100% - 20px);
  height: 90px;
  margin: 0 10px;
  border-top: 2px solid rgba(112, 124, 151, 0.15);
}
.send__input {
  flex-grow: 1;
  height: 100%;
  padding-left: 10px;
  color: #000000;
  font-size: 16px;
}
.send__input::placeholder {
  color: rgba(0, 0, 0, 0.3);
  font-size: 16px;
}
.send__button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: linear-gradient(325deg, #2a8bf2 14.76%, #7cb8f7 87.3%);
  border-radius: 50%;
  overflow: hidden;
  transition: 0.3s ease;
  box-shadow: 4px 4px 25px rgba(42, 139, 242, 0.15), 2px 2px 25px rgba(42, 139, 242, 0.05), 4px 6px 10px rgba(42, 139, 242, 0.15);
  cursor: pointer;
}
.send__button:hover {
  opacity: 0.8;
}
.send__button svg {
  color: #ffffff;
  transform: translateX(-2px) rotate(45deg);
}

.chat {
  position: fixed;
  bottom: 0;
  left: 30px;
  display: flex;
  max-width: calc(100% - 60px);
  width: 700px;
  height: 500px;
  background-color: #ffffff;
  border-radius: 20px 20px 0 0;
  box-shadow: 6px -6px 30px 0 rgba(0, 0, 0, 0.2);
  transform: translateY(100%);
  transition: transform 0.3s ease;
}
@media only screen and (max-width: 576px) {
  .chat {
    left: 10px;
    max-width: calc(100% - 20px);
  }
}
.chat--opened {
  transform: translateY(0);
}
.chat__label {
  position: absolute;
  top: -40px;
  left: 10px;
  width: auto;
  height: 40px;
  padding: 0 15px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 10px;
  border-radius: 10px 10px 0 0;
  background: linear-gradient(90deg, #7cb8f7 0%, #2a8bf2 100%);
  box-shadow: 3px -3px 15px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.chat__label:hover svg {
  transform: scale(-1, 1);
}
.chat__label svg {
  color: #ffffff;
  transition: 0.3s ease;
}
.chat__new-messages-count {
  color: #ffffff;
}
.chat__users {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 70px;
  width: 100%;
  height: 100%;
  padding: 10px 0;
  border-right: 1px solid rgba(112, 124, 151, 0.1);
  overflow-y: auto;
  transition: max-width 0.3s ease;
}
@media only screen and (max-width: 768px) {
  .chat__users {
    max-width: 100%;
    width: 100%;
  }
}
.chat__users--select {
  max-width: 200px;
}
@media only screen and (max-width: 768px) {
  .chat__users--select {
    max-width: 100%;
  }
}
.chat__users::-webkit-scrollbar {
  width: 7px;
}
.chat__users::-webkit-scrollbar-thumb {
  background: #9c9c9c;
  border-radius: 5px;
}
.chat__search {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 90px;
  padding: 0 10px;
  cursor: pointer;
}
.chat__search:hover svg {
  transform: scale(1.1);
}
.chat__search svg {
  transition: 0.3s ease;
}
.chat__search select {
  width: 100%;
  border: 2px solid rgba(112, 124, 151, 0.1);
  box-shadow: 3px 3px 10px 0 rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  padding: 5px 10px;
  cursor: pointer;
}
.chat__user {
  position: relative;
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 5px 0;
  border-top: 2px solid rgba(112, 124, 151, 0.1);
  cursor: pointer;
}
@media only screen and (max-width: 768px) {
  .chat__user {
    align-items: center;
    justify-content: flex-start;
    gap: 15px;
    padding: 10px 40px 10px 20px;
  }
}
.chat__user-group {
  display: none;
}
@media only screen and (max-width: 768px) {
  .chat__user-group {
    display: block;
    max-width: calc(100% - 100px);
  }
}
.chat__user-name {
  font-size: 18px;
  line-height: 1.2;
  text-transform: capitalize;
}
@media only screen and (max-width: 576px) {
  .chat__user-name {
    font-size: 16px;
  }
}
.chat__user-last-message {
  font-size: 14px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.chat__time {
  position: absolute;
  bottom: 4px;
  right: 10px;
  display: none;
  font-size: 10px;
}
@media only screen and (max-width: 768px) {
  .chat__time {
    display: block;
  }
}
.chat__dialog {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
@media only screen and (max-width: 768px) {
  .chat__dialog {
    display: none;
  }
}